import { Component, ViewChild, ElementRef, OnInit } from '@angular/core'
import { CommonModule } from '@angular/common'
import {
  FormControl,
  FormGroup,
  FormsModule,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms'
import {
  IonContent,
  IonInput,
  IonButton,
  IonCol,
  IonInputPasswordToggle,
  IonText,
  IonLabel,
  IonIcon,
  IonItem,
  IonRow,
  ModalController,
  LoadingController,
} from '@ionic/angular/standalone'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { finalize } from 'rxjs'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { AuthService } from 'src/app/services/external/auth.service'
import { RouterLink, ActivatedRoute } from '@angular/router'
import { ToastService, ToastType } from 'src/app/services/utils/toast.service'
import { CountriesComponent } from 'src/app/components/modals/countries/countries.component'
import { Country, CountryService } from 'src/app/services/utils/country.service'
import { PhoneUtilsService } from 'src/app/services/utils/phone.service'
import { addIcons } from 'ionicons'
import { logoApple, logoGoogle } from 'ionicons/icons'

@Component({
  selector: 'app-signin',
  templateUrl: './signin.page.html',
  styleUrls: ['./signin.page.scss'],
  imports: [
    IonContent,
    IonInput,
    IonCol,
    IonText,
    IonRow,
    IonInputPasswordToggle,
    IonItem,
    IonIcon,
    IonButton,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    HeaderComponent,
    RouterLink,
  ],
})
export class SigninPage implements OnInit {
  @ViewChild('phoneInput', { read: ElementRef, static: true })
  phoneInput!: ElementRef<HTMLInputElement>
  public form: FormGroup
  public phoneCodes: Country[] = []
  public currentCountry: Country | null = null

  constructor(
    private translateService: TranslateService,
    private modalController: ModalController,
    private countryService: CountryService,
    private loadingController: LoadingController,
    private route: ActivatedRoute,
    public authService: AuthService,
    private toastService: ToastService,
    private phoneService: PhoneUtilsService
  ) {
    this.form = new FormGroup({
      tel: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
    })
    addIcons({ logoApple, logoGoogle })
  }

  async ngOnInit() {
    try {
      const countries = await this.countryService.getCountries()
      this.currentCountry =
        countries.find((x: Country) => x.code === 'FR') || null

      this.route.queryParams.subscribe((params) => {
        if (params['phone']) {
          const phoneNumber = params['phone']
          const numberWithoutCode = phoneNumber.replace(
            this.currentCountry?.dial_code || '',
            ''
          )
          this.form.controls['tel'].setValue(numberWithoutCode)

          if (this.phoneInput?.nativeElement) {
            const formattedNumber = this.phoneService.formatPhoneNumber(
              numberWithoutCode,
              this.currentCountry as Country
            )
            this.phoneInput.nativeElement.value = formattedNumber
          }
        }
      })

      if (this.currentCountry) {
        const placeHolder = await this.phoneService.getPhoneNumberPattern(
          this.currentCountry
        )
        this.phoneInput.nativeElement.placeholder = placeHolder
      }
    } catch (err) {
      console.error('Erreur lors de la récupération des pays:', err)
    }
  }

  public async openCountryPage() {
    const modal = await this.modalController.create({
      component: CountriesComponent,
      componentProps: { currentPosition: 'FR' },
      initialBreakpoint: 0.75,
      breakpoints: [0, 0.75],
    })

    modal.onDidDismiss().then(async (data) => {
      if (data?.data?.selectedCountry) {
        this.currentCountry = data.data.selectedCountry
        this.form.controls['tel'].setValue('')
        if (this.currentCountry) {
          try {
            const placeHolder = await this.phoneService.getPhoneNumberPattern(
              this.currentCountry
            )
            this.phoneInput.nativeElement.placeholder = placeHolder
          } catch {
            console.warn('Aucun placeholder trouvé')
            this.phoneInput.nativeElement.placeholder =
              this.translateService.instant('GENERIC.phone_number')
          }
        }
      }
    })

    await modal.present()
  }

  public changePhoneNumber(event: Event) {
    const inputElement = event.target as HTMLInputElement
    const inputValue = inputElement.value

    if (this.currentCountry) {
      const numOutput = this.phoneService.formatPhoneNumber(
        inputValue,
        this.currentCountry
      )
      inputElement.value = inputValue.startsWith('0')
        ? inputValue.substring(1)
        : numOutput
    }
  }

  public async signIn() {
    if (
      !this.form.controls['tel'].value.trim() ||
      !this.form.controls['password'].value.trim()
    ) {
      this.toastService.createWithJustMessage(
        this.translateService.instant('error.missing_credentials'),
        ToastType.Error
      )
      return
    }
    const loading = await this.loadingController.create({
      message: this.translateService.instant('generic.please_wait'),
    })
    await loading.present()
    const phone =
      this.currentCountry?.dial_code +
      this.form.value.tel.replace(/[(),\-,\s]/g, '').replace(/^0+/, '')
    const password = this.form.value.password
    this.authService.signIn(phone, password).finally(() => {
      loading.dismiss()
    })
  }
}
